import React, { useContext } from 'react';
import { GatsbyImage } from 'gatsby-plugin-image';
import StorylineContext from '@store/whatsapp-lebanon/storyline-context';
import { motion, AnimatePresence } from 'framer-motion';
import AnimatedBackground from '@components/scrollytelling/whatsapp-lebanon/animated-background';

const BackgroundScene = ({ getImage }) => {
	const { lang, currentScene, activeMessage } = useContext(StorylineContext);

	const duration = 0.5;
	const delay = 0;
	const isEventBackground = activeMessage.backgroundScene === 99 && activeMessage.type === 'event';
	const initialX = lang === 'en-GB' ? '100%' : '-100%';
	const exitX = lang === 'en-GB' ? '-100%' : '100%';

	const renderBackgroundImage = () => (
		<GatsbyImage
			className="absolute top-0 left-0 w-full h-full"
			image={getImage(currentScene?.images.background || 'whatsapp-pattern')}
			alt={`Background image "WhatsApp Lebanon"`}
		/>
	);

	return (
		<AnimatePresence>
			<motion.div
				key={`scene-${currentScene?.title[lang]}`}
				initial={{ x: initialX, opacity: 0 }}
				animate={{ x: 0, opacity: isEventBackground ? 0.7 : 1 }}
				exit={{ x: exitX, opacity: 0 }}
				transition={{ duration, delay }}
				className="top-0 left-0 absolute w-full h-full z-30 bg-gradient-to-b from-gray-500 to-gray-600"
			>
				{renderBackgroundImage()}
				{activeMessage.fullScreenEvent && <AnimatedBackground getImage={getImage} />}
			</motion.div>
		</AnimatePresence>
	);
};

export default BackgroundScene;
