import React, { useState, useContext } from 'react'
import Select from 'react-select'
import moment from 'moment'
import '@components/scrollytelling/whatsapp-lebanon/ar-locale'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'
import CHARACTERS from '@data/whatsapp-lebanon/character-data'

let options = [
	{ value: 'chocolate', label: 'Chocolate' },
	{ value: 'strawberry', label: 'Strawberry' },
	{ value: 'vanilla', label: 'Vanilla' },
]

const DropdownComponent = () => {
	const [selectedOption, setSelectedOption] = useState(null)

	const { currentLangData, lang, jumpToMessage, allMessages } = useContext(StorylineContext)

	// const moment = momentLocale

	let events = []

	const changeDate = (e) => {
		const val = e.value
		setSelectedOption(val)
		jumpToMessage(val)
	}

	if (allMessages) {
		for (let i = 0; i < allMessages.length; i++) {
			let eventLabel =
				'💬 ' +
				moment(allMessages[i].timestamp * 1000)
					.locale(lang === 'ar-LB' ? 'ar' : 'en')
					.format(lang === 'ar-LB' ? 'LTS' : 'D.M.YY') +
				' ' +
				CHARACTERS[allMessages[i].from.characterId].name[lang]

			if (allMessages[i].type === 'photo') {
				eventLabel =
					'📷 ' +
					moment(allMessages[i].timestamp * 1000)
						.locale(lang === 'ar-LB' ? 'ar' : 'en')
						.format(lang === 'ar-LB' ? 'MM.DD.YYYY' : 'D.M.YY') +
					' ' +
					CHARACTERS[allMessages[i].from.characterId].name[lang]
			}

			if (allMessages[i].type === 'event') {
				eventLabel = '🗓️ ' + allMessages[i].eventTitle[lang]
			}

			events.push({
				value: allMessages[i].timestamp,
				label: eventLabel,
			})
		}
	}

	if (events.length > 0) {
		options = events
	}

	return (
		<Select
			className={'w-1/2 z-50'}
			defaultValue={selectedOption}
			value={null}
			isSearchable={false}
			onChange={changeDate}
			options={options}
			isRtl={lang === 'ar-LB'}
			placeholder={currentLangData?.app.chooseMessage || 'Select...'}
			// escapeClearsValue
			// isClearable
		/>
	)
}

export default DropdownComponent
