import React, { useContext, useEffect, useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import HorizontalTimeline from '@components/scrollytelling/whatsapp-lebanon/horizontal-timeline'
import IntroChapters from '@components/scrollytelling/whatsapp-lebanon/intro-chapters'
import LanguageSwitch from '@components/scrollytelling/whatsapp-lebanon/language-switch'
import { isMobile } from 'react-device-detect'

import StorylineContext from '@store/whatsapp-lebanon/storyline-context'

const StoryContent = ({ defaultLanguage, getImage }) => {
	const { timelineIsActive, isIOS, setLang } = useContext(StorylineContext)
	const [isMobileSSR, setIsMobileSSR] = useState(false)

	useEffect(() => {
		setLang(defaultLanguage)
		setIsMobileSSR(isMobile)
	}, [])

	return (
		<div
			className={`relative w-screen h-screen-mobile h-screen-ios sm:h-screen-desktop sm:min-h-screen-desktop flex flex-col justify-center items-center ${timelineIsActive ? 'bg-gradient-to-b from-gray-500 to-gray-600' : ''
				}`}
			key={`story-content-${timelineIsActive}`}
		>
			{/* START Language controller */}
			<>
				<div className={'hidden sm:block top-10 left-10 absolute z-50'}>
					<LanguageSwitch />
				</div>
				<div className={'block sm:hidden bottom-0 left-0 w-full absolute z-50'}>
					<LanguageSwitch />
				</div>
			</>
			{/* END language controller */}
			<AnimatePresence>
				{timelineIsActive ? (
					<motion.div
						initial={{ opacity: 0 }}
						animate={{ opacity: timelineIsActive ? 1 : 0 }}
						exit={{ opacity: 0 }}
						className={`sm:py-28 w-full ${timelineIsActive ? '' : 'pointer-events-none'}`}
						id='wa-lebanon-main-wrapper'
						style={{
							maxHeight: !isMobileSSR ? 'inherit' : isIOS ? 'inherit' : 'calc(100svh - 60px)',
						}}
					>
						<HorizontalTimeline getImage={getImage} />
					</motion.div>
				) : (
					<div id='wa-lebanon-intro-wrapper'>
						<IntroChapters getImage={getImage} />
					</div>
				)}
			</AnimatePresence>
		</div>
	)
}

export default StoryContent
