import React, { useContext } from 'react'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'

export default function LanguageSwitch() {
	const { switchLang, lang } = useContext(StorylineContext)

	const [langSSR, setLangSSR] = React.useState('en-GB')

	React.useEffect(() => {
		setLangSSR(lang)
	}, [])

	return (
		<>
			<span className='z-50 items-center justify-center hidden shadow-sm pointer-events-auto sm:inline-flex'>
				<button
					type='button'
					className={`relative inline-flex items-center px-4 py-2 text-xl font-medium ${langSSR === 'en-GB' ? 'text-white bg-gray-900' : 'text-gray-700 bg-white'
						} border border-gray-300 hover:opacity-80 focus:z-10 focus:outline-none focuss:ring-1 focus:ring-red-500 focus:border-red-500`}
					onClick={() => switchLang('en-GB')}
				>
					English
				</button>
				<button
					type='button'
					className={`relative inline-flex items-center px-4 py-2 -ml-px text-xl font-medium ${langSSR === 'ar-LB' ? 'text-white bg-gray-900' : 'text-gray-700 bg-white'
						} border border-gray-300 hover:opacity-80 focus:z-10 focus:outline-none focus:ring-1 focus:ring-red-500 focus:border-red-500`}
					onClick={() => switchLang('ar-LB')}
				>
					العربية
				</button>
			</span>
			<span
				className='absolute bottom-0 left-0 z-50 flex items-center justify-center w-full p-4 shadow-xl pointer-events-auto sm:p-0 sm:bottom-24 opacity-70 sm:hidden'
				id={'lang-switch'}
			>
				<button
					type='button'
					className={`relative z-50 pointer-events-auto inline-flex items-center px-4 py-2 text-xl font-medium ${langSSR === 'en-GB' ? 'text-white bg-gray-900' : 'text-gray-700 bg-white'
						} border border-gray-300 hover:opacity-80 focus:z-10 focus:outline-none focuss:ring-1 focus:ring-red-500 focus:border-red-500`}
					onTouchStart={() => switchLang('en-GB')}
				>
					English
				</button>
				<button
					type='button'
					className={`relative z-50 pointer-events-auto inline-flex items-center px-4 py-2 -ml-px text-xl font-medium ${langSSR === 'ar-LB' ? 'text-white bg-gray-900' : 'text-gray-700 bg-white'
						} border border-gray-300 hover:opacity-80 focus:z-10 focus:outline-none focus:ring-1 focus:ring-red-500 focus:border-red-500`}
					onTouchStart={() => switchLang('ar-LB')}
				>
					عربي
				</button>
			</span>
		</>
	)
}
