import React, { useState, useMemo, useEffect } from 'react'
import REAL_DATA from '@data/whatsapp-lebanon/data'
import SCENES from '@data/whatsapp-lebanon/scene-data'
import moment from 'moment'
import { isMobile } from 'react-device-detect'
import scrollToElement from '@components/scrollytelling/scroll-to-element'

const StorylineContext = React.createContext({
	/* Language related */
	lang: 'en-GB',
	currentLangData: {},
	switchLang: () => {},
	/* Storyline related */
	currentMessage: 0,
	setCurrentMessage: () => {},
	showHowToUse: false,
	setShowHowToUse: () => {},
	showMessageNextTime: false,
	setShowHowToUse: () => {},
	jumpToMessage: () => {},
	allMessages: [],
	activeMessage: {},
	previousMessage: () => {},
	nextMessage: () => {},
	currentScene: {},
	setCurrentScene: () => {},
	allScenes: [],
	percentageDone: 0,
	scrollToElement: () => {},
	currentDir: 'ltr',
	isFullScreenSlide: false,
	timelineIsActive: false,
	activateTimeline: () => {},
	deactivateTimeline: () => {},
	currentEvent: {},
	setCurrentEvent: () => {},
	personBioIsOpen: false,
	setPersonBioIsOpen: () => {},
	allEvents: [],
	allNonEvents: [],
	currentIntroChapter: 0,
	setCurrentIntroChapter: () => {},
	momentLocale: moment,
	isIOS: false,
	setIsIOS: () => {},
	setLang: () => {},
})

export default StorylineContext

export function StorylineContextProvider(props) {
	const [lang, setLang] = useState(typeof window === 'undefined' ? 'en-GB' : window.location.pathname.indexOf('wtsb-lbnn') > -1 ? 'ar-LB' : 'en-GB')
	const [currentMessage, setCurrentMessage] = useState(0)
	const [showHowToUse, setShowHowToUse] = useState(false)
	const [showMessageNextTime, setShowMessageNextTime] = useState(true)
	const [allMessages, setAllMessages] = useState(REAL_DATA)
	const [allScenes, setAllScenes] = useState(SCENES)
	const [currentScene, setCurrentScene] = useState(SCENES.filter((el) => el.id === allMessages[currentMessage].backgroundScene)[0])
	const [percentageDone, setPercentageDone] = useState(0)
	const [isFullScreenSlide, setIsFullScreenSlide] = useState(false)
	const [timelineIsActive, setTimelineIsActive] = useState(false)
	const [currentEvent, setCurrentEvent] = useState(null)
	const [allEvents, setAllEvents] = useState(REAL_DATA.filter((el) => el.type === 'event'))
	const [personBioIsOpen, setPersonBioIsOpen] = useState(false)
	const [currentIntroChapter, setCurrentIntroChapter] = useState(0)
	const [momentLocale, setMomentLocale] = useState(moment)
	const [isIOS, setIsIOS] = useState(false)

	useMemo(() => {
		const currentLang = typeof window === 'undefined' ? 'en-GB' : window.location.pathname.indexOf('wtsb-lbnn') > -1 ? 'ar-LB' : 'en-GB'
		// console.log(currentLang)
		setLang(currentLang)
	}, [])

	/* Set isIOS if is IOS */
	useEffect(() => {
		setIsIOS(typeof navigator?.standalone === 'boolean')
	}, [])

	/* Set current locale for moment and write to global context state */
	useEffect(() => {
		setMomentLocale(moment.locale('en-GB'))
		if (lang.indexOf('en') > -1) {
			// console.log('Set moment locale to EN')
			setMomentLocale(moment.locale('en-GB'))
		} else {
			// console.log('Set moment locale to AR')
			setMomentLocale(moment.locale('ar'))
		}
	}, [lang])

	/* Scroll to timeline when active */
	useEffect(() => {
		if (timelineIsActive) {
			scrollToElement('horizontal-timeline', 0, 50, 'ease-in', isMobile ? -60 : -80)
		}
	}, [currentMessage, timelineIsActive, isMobile])

	/* Show timeline */
	const activateTimeline = () => {
		setTimelineIsActive(true)
	}

	/* Hide timeline */
	const deactivateTimeline = () => {
		setTimelineIsActive(false)
	}

	/* Detect if slide is full screen */
	useEffect(() => {
		if (allMessages[currentMessage].type === 'event' || allMessages[currentMessage].isFullScreenSlide) {
			setIsFullScreenSlide(true)
		} else {
			setIsFullScreenSlide(false)
		}
	}, [currentMessage])

	/* Write percentage done progress to state */
	useEffect(() => {
		setPercentageDone(parseInt((currentMessage / (allMessages.length - 1)) * 100))
	}, [currentMessage])

	/* Write all messages and scenes to context state */
	useEffect(() => {
		setAllScenes(SCENES)
		setAllMessages(REAL_DATA)
	}, [REAL_DATA, SCENES])

	/* Detect current scene */
	useEffect(() => {
		setCurrentScene(SCENES.filter((el) => el.id === allMessages[currentMessage].backgroundScene)[0])
	}, [currentMessage])

	/* Change to new event if currentMessage IS of type event */
	useEffect(() => {
		if (allMessages[currentMessage].type === 'event') {
			setCurrentEvent(allMessages[currentMessage])
		}
		if (allEvents.filter((el) => el.date === allMessages[currentMessage].date).length > 0) {
			setCurrentEvent(allEvents.filter((el) => el.date === allMessages[currentMessage].date)[0])
		}
	}, [currentMessage, allEvents])

	/* Jump between messages */
	const jumpToMessage = (timestamp) => {
		const indexOfMessageToJumpTo = allMessages.findIndex((el) => el.timestamp === timestamp)
		setCurrentMessage(indexOfMessageToJumpTo)
		// const scrollToElement = (element, delay, duration, smooth, offset) => {
		scrollToElement('horizontal-timeline', 0, 50, 'ease-in', isMobile ? -60 : -80)
	}

	/* Go to previous message AND also check if events needs changing */
	const previousMessage = () => {
		if (currentMessage <= 0) {
			/* Don't do anything if there is no previous message */
			return
		}
		/* Find out if events needs changing */
		if (allEvents && currentMessage !== 0) {
			// console.log(currentEvent)
			/* Compare current message's timestamp with current event's timestamp */
			if (allMessages[currentMessage - 1].timestamp * 1000 < currentEvent?.timestamp * 1000) {
				/* Get index of current event in events array */
				const currentEventIdx = allEvents.indexOf(currentEvent)
				/* Decrease current Event by one index and save to context */
				setCurrentEvent(allEvents[currentEventIdx - 1])
			}
		}
		/* Set currentMessage to previous */
		setCurrentMessage(currentMessage - 1)
		scrollToElement('horizontal-timeline', 0, 50, 'ease-in', isMobile ? -60 : -80)
	}

	/* Go to next message */
	const nextMessage = () => {
		if (currentMessage + 1 >= allMessages.length) {
			return
		}
		setCurrentMessage(currentMessage + 1)
		scrollToElement('horizontal-timeline', 0, 50, 'ease-in', isMobile ? -60 : -80)
	}

	/* Detect if tutorial was hidden in the last five days */
	useEffect(() => {
		/* HIDE TUTORIAL ALWAYS */
		setShowHowToUse(false)
		return
		/* ENDS HERE */
		if (window.localStorage.getItem('hideTutorial')) {
			const lastHidden = JSON.parse(window.localStorage.getItem('hideTutorial'))
			const nowFiveDaysAgo = moment().subtract('5', 'days')
			const lastTimeUserHasHiddenTutorial = moment(lastHidden)
			if (nowFiveDaysAgo.isBefore(lastTimeUserHasHiddenTutorial)) {
				// console.log('Hide tutorial was chosen less than five days ago')
				setShowHowToUse(false)
			}
		} else {
			setShowHowToUse(true)
		}
	}, [timelineIsActive])

	/* Switch between languages */
	const switchLang = (ln) => {
		window.localStorage.setItem('appUILang', ln)
		setLang(ln)
	}

	/* Language data for translation of UI stuff */
	const langData = {
		'en-GB': {
			app: {
				storyTitle: 'WhatsApp, Lebanon?',
				storyLead: 'A different kind of timeline, a different sort of story',
				selectLanguage: 'Switch language',
				prevButton: 'Back',
				nextButton: 'Next',
				readNextMessage: 'Go forward →',
				timeline: 'Timeline',
				goToTimeline: 'Timeline →',
				readMore: '↑ Read more',
				readLess: '↓ Hide information ',
				from: 'From',
				to: 'To',
				swipeCallToAction: 'Swipe to change slide',
				chooseMessage: 'Choose date',
				openImageInNewWindow: 'Open image in new window →',
				image: 'Image:',
				allWhatsAppers: 'All WhatsAppers',
				about: 'Meet',
				aboutTheWhatsAppersIntroText: 'Click on the avatars to read more about their background.',
				aboutThisStory: 'About this story',
				meetTheWhatsAppers: 'Meet the WhatsAppers',
				restartButton: 'Restart',
				startButton: 'Start',
				thisIsTheEnd: 'Credits',
				startTimeline: 'Begin with timeline',
			},
			languages: {
				arabic: 'Arabic',
				english: 'English',
			},
		},
		'ar-LB': {
			app: {
				storyTitle: 'واتساب لبنان؟',
				storyLead: 'لكل زمن فريد قصة فريدة',
				selectLanguage: 'اختر لغة',
				prevButton: 'رجوع',
				nextButton: 'التّالي',
				readNextMessage: '← الرسالة التالية',
				timeline: 'التسلسل الزمني',
				goToTimeline: 'الجدول الزمني ←',
				readMore: '↑ Read more (AR)',
				readLess: '↓ Hide information (AR)',
				from: 'من',
				to: 'إلى',
				swipeCallToAction: 'سحب الشاشة للانتقال إلى الصفحة التالية',
				chooseMessage: 'اختيار رسالة',
				openImageInNewWindow: 'Open image in new window (AR) →',
				image: 'الصورة',
				allWhatsAppers: 'All WhatsAppers',
				about: 'تعرّفوا على',
				aboutTheWhatsAppersIntroText: 'Click on the avatars to read more about their background.',
				aboutThisStory: 'حول هذه القصة',
				meetTheWhatsAppers: 'تعرفوا إلى المساهمين',
				restartButton: 'العودة إلى الصفحة الأولى',
				startButton: 'افتتاح',
				thisIsTheEnd: 'طاقم العمل',
				startTimeline: 'البداية',
			},
			languages: {
				arabic: 'عربى',
				english: 'الإنجليزية',
			},
		},
	}

	return (
		<StorylineContext.Provider
			value={{
				/* Language related */
				lang,
				switchLang,
				currentLangData: langData[lang] || langData['en-GB'],
				/* Storyline related */
				currentMessage,
				setCurrentMessage,
				showHowToUse,
				setShowHowToUse,
				showMessageNextTime,
				setShowMessageNextTime,
				jumpToMessage,
				allMessages,
				activeMessage: allMessages[currentMessage],
				previousMessage,
				nextMessage,
				currentScene,
				activeScene: allScenes[currentScene],
				allScenes,
				setCurrentScene,
				percentageDone,
				scrollToElement,
				currentDir: lang === 'ar-LB' ? 'rtl' : 'ltr',
				isFullScreenSlide,
				timelineIsActive,
				activateTimeline,
				deactivateTimeline,
				currentEvent,
				setCurrentEvent,
				personBioIsOpen,
				setPersonBioIsOpen,
				allEvents,
				allNonEvents: allMessages.filter((el) => el.type !== 'event'),
				currentIntroChapter,
				setCurrentIntroChapter,
				momentLocale,
				isIOS,
				setIsIOS,
				setLang,
			}}
		>
			{props.children}
		</StorylineContext.Provider>
	)
}
