import React, { useContext, useCallback, useState, useEffect } from 'react';
import StorylineContext from '@store/whatsapp-lebanon/storyline-context';
import useKeypress from 'react-use-keypress';
import { motion, AnimatePresence } from 'framer-motion';

import BackgroundScene from '@components/scrollytelling/whatsapp-lebanon/background-scene';
import CharacterScene from '@components/scrollytelling/whatsapp-lebanon/character-scene';
import ChapterControl from '@components/scrollytelling/whatsapp-lebanon/chapter-control';
import NotificationCentre from '@components/scrollytelling/whatsapp-lebanon/notification-centre';

const HorizontaTimeline = ({ getImage }) => {
	const {
		lang,
		switchLang,
		previousMessage,
		nextMessage,
		currentMessage,
		allMessages,
		setCurrentIntroChapter,
		deactivateTimeline,
		activeMessage,
		jumpToMessage,
	} = useContext(StorylineContext);

	const [isDebouncing, setIsDebouncing] = useState(false);
	const [actionCount, setActionCount] = useState(0);
	const debounceDelay = 500; // debounce delay in milliseconds

	useEffect(() => {
		let timer;

		if (actionCount > 0) {
			timer = setTimeout(() => {
				setActionCount(0);
				setIsDebouncing(false);
			}, debounceDelay);
		}

		return () => clearTimeout(timer);
	}, [actionCount]);

	const handleAction = (actionFunction) => {
		if (isDebouncing) return;

		if (actionCount < 3) {
			actionFunction();
			setActionCount((prev) => prev + 1);
		} else {
			setIsDebouncing(true);
		}
	};

	const arrowRightFunction = () => {
		handleAction(() => {
			if (lang === 'en-GB') {
				if (currentMessage + 1 < allMessages.length) {
					nextMessage();
				} else {
					setCurrentIntroChapter(3);
					deactivateTimeline();
				}
			} else {
				if (currentMessage > 0) {
					previousMessage();
				} else {
					setCurrentIntroChapter(2);
					deactivateTimeline();
				}
			}
		});
	};

	const arrowLeftFunction = () => {
		handleAction(() => {
			if (lang === 'en-GB') {
				if (currentMessage > 0) {
					previousMessage();
				} else {
					setCurrentIntroChapter(2);
					deactivateTimeline();
				}
			} else {
				if (currentMessage + 1 < allMessages.length) {
					nextMessage();
				} else {
					setCurrentIntroChapter(3);
					deactivateTimeline();
				}
			}
		});
	};

	useKeypress(['L', 'l', 'ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'Escape'], (event) => {
		if (event.key === 'Escape') {
			if (activeMessage.id === 0) {
				setCurrentIntroChapter(2);
				deactivateTimeline();
			} else {
				const firstMessage = allMessages.find((el) => el.id === 0);
				jumpToMessage(firstMessage.timestamp);
			}
		}
		if (event.key === 'ArrowLeft') {
			arrowLeftFunction();
		}
		if (event.key === 'ArrowRight') {
			arrowRightFunction();
		}
		if (event.key === 'ArrowDown') {
			nextMessage();
		}
		if (event.key === 'ArrowUp') {
			previousMessage();
		}
		if (event.key === 'L' || event.key === 'l') {
			switchLang(lang === 'ar-LB' ? 'en-GB' : 'ar-LB');
		}
	});

	return (
		<div
			key={lang}
			className={`relative flex flex-col items-center justify-center h-screen-ios min-h-screen-ios min-h-screen sm:min-h-screen-desktop bg-gray-700 overflow-hidden bg-gradient-to-b from-gray-500 to-gray-600 ${isDebouncing ? 'opacity-80' : 'opacity-100'}`}
		>
			<AnimatePresence>
				<>
					<motion.div className="absolute top-20 mt-14 sm:mt-0 sm:top-0 left-0 w-full pt-sixteenbynine sm:pt-0 sm:h-full text-4xl text-white font-bold overflow-hidden bg-gradient-to-b from-gray-500 to-gray-600">
						<BackgroundScene getImage={getImage} />
						<CharacterScene getImage={getImage} />
					</motion.div>
					<NotificationCentre getImage={getImage} />
				</>
			</AnimatePresence>
			<ChapterControl isDebouncing={isDebouncing} />
		</div>
	);
};

export default HorizontaTimeline;
