import React from 'react'
import parse from 'html-react-parser'

// const parse = require('html-react-parser');

const RichText = ({ text, className, style }) => {
	const { domToReact } = parse

	const textIsString = typeof text === 'string'

	return textIsString ? (
		<div className={className} style={style}>
			{parse(text, {
				replace: (domNode) => {
					if (domNode.attribs && domNode.attribs.style) {
						try {
							style(domNode.attribs.style)
						} catch (error) {
							// delete the attribute that's causing the error
							// then convert the dom node to react
							delete domNode.attribs.style
							return domToReact(domNode)
						}
					}
				},
			})}
		</div>
	) : (
		'<span>ERROR</span>'
	)
}

export default RichText
