import React, { useContext, useState, useEffect } from 'react'

import { motion } from 'framer-motion'
import moment from 'moment'
import '@components/scrollytelling/whatsapp-lebanon/ar-locale'
import { isMobile } from 'react-device-detect'
import RichText from '@components/scrollytelling/whatsapp-lebanon/rich-text'

import StorylineContext from '@store/whatsapp-lebanon/storyline-context'

// const isIOS = typeof navigator?.standalone === 'boolean'

const EventBox = () => {
	const { currentEvent, currentDir, lang, activeMessage, isFullScreenSlide, allMessages, isIOS } = useContext(StorylineContext)
	const [isOpen, setIsOpen] = useState(activeMessage.id === currentEvent?.id)
	const [currentRotation, setCurrentRotation] = useState(Math.floor(Math.random() * (1.5 - -1.5 + 1)) + -1.5)

	const activeEvent = allMessages.filter((el) => el.type === 'event' && el.timestamp === activeMessage.timestamp)[0]

	useEffect(() => {
		setCurrentRotation(Math.floor(Math.random() * (1.5 - -1.5 + 1)) + -1.5)
	}, [currentEvent])

	useEffect(() => {
		setIsOpen(activeMessage.id === activeEvent?.id)
	}, [activeMessage, currentEvent])

	return (
		<motion.div
			initial={{ opacity: 0 }}
			// enter={{ opacity: 1 }}
			animate={{ opacity: 1 }}
			className={'hand-drawn z-50'}
			key={`static-event-box-scene-${activeMessage.backgroundScene}-event-${currentEvent?.id}`}
			transition={{ delay: activeMessage.fullScreenEvent ? 3 : 0.5 }}
		>
			<motion.div
				className={`block absolute bottom-auto sm:bottom-auto ${isIOS ? 'mt-10' : 'mt-0'} sm:mt-0 sm:top-28 ${
					activeEvent?.id === activeMessage.id ? 'left-0' : 'right-4'
				} sm:left-10 sm:w-2/5 p-5 sm:p-8 shadow-2xl z-40 ${isOpen ? 'z-50' : '-z-1'} ${
					isFullScreenSlide ? 'top-1/2' : isIOS ? 'top-80 mt-2 sm:mt-0' : 'top-40 mt-2 sm:mt-0'
				}`}
				initial={{
					y: '-150%',
					opacity: 0,
				}}
				animate={{
					y: 0,
					opacity: 1,
					rotate: [0, 0, currentRotation],
					background: '#e3be7e',
				}}
				// enter={{ y: 0, opacity: 1 }}
				exit={{ y: '-150%', opacity: 0 }}
				key={`event-box-${activeMessage.backgroundScene}`}
				transition={{ delay: 0.25, type: 'spring', damping: 8, stiffness: 120 }}
			>
				<motion.div
					transition={{ delay: 0.5 }}
					initial={{ x: activeEvent?.id === activeMessage.id || (isMobile && isOpen) ? -50 : 0, opacity: 0 }}
					// enter={{ x: 0, opacity: 1 }}
					animate={{ x: 0, opacity: 1 }}
					dir={currentDir}
					className={`flex mb-2 sm:mb-8 text-3xl ${lang === 'ar-LB' ? '' : 'comic-font'} z-50`}
				>
					<span className={'text-2xl sm:text-5xl px-2 py-1 bg-gray-800 font-bold text-white rounded'}>
						{moment(currentEvent?.date)
							.locale(lang === 'ar-LB' ? 'ar' : 'en-GB')
							.format('D MMMM YYYY')}
					</span>
				</motion.div>
				<motion.div
					transition={{ delay: 0.7 }}
					initial={{ x: -25, opacity: 0 }}
					// enter={{ x: 0, opacity: 1 }}
					// animate={{ x: 0, opacity: isOpen ? 1 : 0.5 }}
					animate={{ x: 0, opacity: 1 }}
					className={'text-black'}
					dir={currentDir}
				>
					{/* DESKTOP */}
					<h2 className={'hidden sm:block mb-4 sm:mb-4 sm:text-5xl'}>
						{activeMessage.type === 'event' ? activeMessage?.eventTitle[lang] : currentEvent?.eventTitle[lang]}
					</h2>
					{/* MOBILE with CHEVRON */}
					<h2
						className={`block sm:hidden ${isOpen ? 'mb-4' : ''} sm:mb-4 sm:text-5xl`}
						onClick={() => {
							isFullScreenSlide ? null : setIsOpen(!isOpen)
						}}
					>
						{activeMessage.type === 'event' ? activeMessage?.eventTitle[lang] : currentEvent?.eventTitle[lang]}
						{/* {isMobile && !isFullScreenSlide && <Icon className={'w-9 h-9'} icon={isOpen ? 'chevron-down' : 'chevron-right'} />} */}
					</h2>
					{/* {isMobile && isOpen && activeEvent && parse(activeEvent?.text[lang])} */}
					{isMobile && isOpen && activeEvent && (
						<RichText className={'text-black inherit-font'} text={activeMessage.type === 'event' ? activeMessage?.text[lang] : currentEvent?.text[lang]} />
					)}
					{/* {!isMobile && activeEvent && <div className={'hidden sm:block text-black inherit-font'}>{parse(activeEvent.text[lang])}</div>} */}
					{!isMobile && (
						<div className={'hidden sm:block text-black inherit-font'}>
							<RichText
								style={{ color: 'black' }}
								className={'text-black inherit-font'}
								text={activeMessage.type === 'event' ? activeMessage?.text[lang] : currentEvent?.text[lang]}
							/>
						</div>
					)}
				</motion.div>
			</motion.div>
		</motion.div>
	)
}

export default EventBox
