import React from 'react'
import { motion } from 'framer-motion'

const VideoViewer = ({ caption, video }) => {
	return (
		<motion.div
			initial={{ opacity: 0 }}
			animate={{ opacity: 1 }}
			// enter={{ opacity: 1 }}
		>
			<video loop autoPlay muted className={'w-full'}>
				<source src={video} />
			</video>
			{caption && <p className={'pt-3 text-2xl'}>{caption}</p>}
		</motion.div>
	)
}

export default VideoViewer
