import React, { useContext } from 'react'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'
import { Tooltip as ReactTooltip } from 'react-tooltip'
import moment from 'moment'
import DropdownComponent from '@components/scrollytelling/whatsapp-lebanon/dropdown'
import { motion } from 'framer-motion'

const ChapterControl = ({ isDebouncing }) => {
	const {
		currentLangData,
		currentMessage,
		lang,
		previousMessage,
		nextMessage,
		jumpToMessage,
		allMessages,
		percentageDone,
		activeMessage,
		setCurrentIntroChapter,
		deactivateTimeline,
	} = useContext(StorylineContext)

	const TimelineIconEvent = ({ width }) => {
		return (
			<svg className={'fill-current'} xmlns='http://www.w3.org/2000/svg' width='9' height='32' fill='none' viewBox='0 0 9 32'>
				<path fill='#FDE68A' className={'fill-current'} d='M0 2H9V30H0z'></path>
				<path fill='#000' d='M3 6H6V26H3z'></path>
			</svg>
		)
	}

	const TimelineIconMessage = ({ width }) => {
		return (
			<svg xmlns='http://www.w3.org/2000/svg' width={width || 1} height='32' fill='none' viewBox='0 0 1 32'>
				<path className={'fill-current'} fill='#000' d='M0 7H1V25H0z'></path>
			</svg>
		)
	}

	return (
		<>
			{/* START desktop */}
			<nav
				className={`transition-opacity duration-75 hidden sm:flex sm:mr-10 sm:ml-3 ${isDebouncing ? 'cursor-not-allowed opacity-60' : ''} absolute bottom-0 left-0 right-0 mb-3 font-bold z-50 shadow-2xl justify-between text-white hand-drawn-el ${lang === 'en-GB' ? 'flex-row' : 'flex-row-reverse'
					} m-5`}
				style={{
					background: '#e3be7e',
					// borderColor: isDebouncing ? 'transparent' : null
				}}
			>
				<ReactTooltip
					id='timeline-tooltip'
					getContent={(dataTip) => dataTip}
					effect='solid'
					delayHide={500}
					delayShow={50}
					delayUpdate={0}
					place={'top'}
					type={'dark'}
					className={'text-2xl lg:text-3xl comic-font'}
					// border={true}
				/>
				<div className={`flex ${lang === 'en-GB' ? 'flex-row' : 'flex-row-reverse'} items-center px-5 flex-grow-1 cursor-pointer`} style={{ flexGrow: 1 }}>
					<p
						onClick={() => {
							jumpToMessage(allMessages.filter((el) => el.id === 0)[0].timestamp)
						}}
						className={`${lang === 'ar-LB' ? 'text-black pl-5' : 'text-black pr-5'} lg:hidden xl:block comic-font whitespace-nowrap`}
					>
						{currentLangData?.app.timeline}{' '}
					</p>
					<div className={`flex ${lang === 'ar-LB' ? 'flex-row-reverse' : 'flex-row'} w-full justify-between flex-grow-1`}>
						{allMessages.map((event, i) => {
							if (event.bigEventScene || event.type === 'event') {
								return (
									<motion.div
										key={`event-message-${i}`}
										data-for='timeline-tooltip'
										data-tip={`📅 ${event.eventTitle[lang]}` || `EVENT TITLE MISSING`}
										onClick={() => jumpToMessage(event.timestamp)}
										className={`relative cursor-pointer`}
										animate={{
											color: event.id === activeMessage.id ? '#FFF' : '#FDE68A',
										}}
										transition={{ type: 'keyframes', duration: 0.25 }}
									>
										<TimelineIconEvent />
									</motion.div>
								)
							} else {
								return (
									<motion.div
										data-for='timeline-tooltip'
										key={`not-event-message-${i}`}
										data-tip={`${event.type === 'photo' || event.type === 'video'
											? `📷 ${moment(event.date).format('D MMM YY')}`
											: `💬 ${moment(event.date).format('D MMM YY')}`
											}`}
										onClick={() => jumpToMessage(event.timestamp)}
										initial={{ scaleY: 1 }}
										animate={{
											scaleY: event.id === activeMessage.id ? 1.5 : 1,
											scaleX: event.id === activeMessage.id ? 3 : 1,
											color: event.id === activeMessage.id ? 'rgb(255,255,255)' : 'rgb(0,0,0)',
											zIndex: event.id === activeMessage.id ? 99 : 10,
											opacity: 1,
										}}
										transition={{ type: 'keyframes', duration: 0.1 }}
										className={`hidden lg:inline-flex cursor-pointer w-auto hover:text-white ${event.id === activeMessage.id ? 'z-50' : 'z-20'}`}
									>
										<TimelineIconMessage />
									</motion.div>
								)
							}
						})}
					</div>
				</div>
				<div className={`flex items-center flex-row gap-10 mx-4 px-5 ${lang === 'en-GB' ? 'flex-row' : 'flex-row-reverse'}`}>
					{currentMessage > 0 ? (
						<div className={'cursor-pointer py-4 text-black comic-font group'} onClick={() => previousMessage()}>
							<span>
								{lang === 'en-GB' && <kbd className={'mx-2'}>←</kbd>} {currentLangData?.app.prevButton} {lang === 'ar-LB' && <kbd className={'mx-2'}>→</kbd>}
							</span>
						</div>
					) : (
						<div
							className={'cursor-pointer py-4 text-black comic-font'}
							onClick={() => {
								setCurrentIntroChapter(2)
								deactivateTimeline()
							}}
						>
							<span>
								{lang === 'en-GB' && <kbd className={'mx-2'}>←</kbd>} {currentLangData?.app.prevButton} {lang === 'ar-LB' && <kbd className={'mx-2'}>→</kbd>}
							</span>
						</div>
					)}
					{currentMessage + 1 < allMessages.length ? (
						<div className={'cursor-pointer py-4 text-black comic-font'} onClick={() => nextMessage()}>
							{lang === 'ar-LB' && <kbd className={'mx-2'}>←</kbd>} {currentLangData?.app.nextButton} {lang === 'en-GB' && <kbd className={'mx-2'}>→</kbd>}
						</div>
					) : (
						<div
							className={'cursor-pointer py-4 text-black'}
							onClick={() => {
								setCurrentIntroChapter(3)
								deactivateTimeline()
							}}
						>
							{lang === 'ar-LB' && <kbd className={'mx-2'}>←</kbd>} {currentLangData?.app.nextButton} {lang === 'en-GB' && <kbd className={'mx-2'}>→</kbd>}
						</div>
					)}
				</div>
			</nav>
			{/* END desktop */}
			{/* START mobile */}
			<div
				className={`flex sm:hidden absolute top-8 p-2 h-5 w-full bg-transparent justify-around items-center z-50 ${lang === 'en-GB' ? 'flex-row' : 'flex-row-reverse'
					}`}
			>
				<p
					onClick={() => {
						if (currentMessage > 0) {
							previousMessage()
						} else {
							setCurrentIntroChapter(3)
							deactivateTimeline()
						}
					}}
					className={'bg-gray-800 px-3 py-1 text-white font-bold'}
				>
					{currentLangData?.app.prevButton}
				</p>
				<DropdownComponent />
				{currentMessage + 1 < allMessages.length ? (
					<p onClick={() => nextMessage()} className={'text-white font-bold px-3 py-1 bg-gray-800'}>
						{currentLangData?.app.nextButton}
					</p>
				) : (
					<p
						onClick={() => {
							setCurrentIntroChapter(3)
							deactivateTimeline()
						}}
						className={'text-white font-bold px-3 py-1 bg-gray-800'}
					>
						{currentLangData?.app.nextButton}
					</p>
				)}
			</div>
			{/* END mobile */}
			{/* START mobile timeline */}
			{lang === 'en-GB' ? (
				<div className={'absolute sm:hidden top-20 flex items-center justify-center w-full max-w-full flex-row p-3 bg-gray-900 z-40'}>
					<div className={'relative bg-gradient-to-r from-gray-700 via-gray-600 to-gray-900 inset-0 h-8 w-full rounded-lg'}>
						<motion.span
							className={`absolute bg-gray-200 z-40 h-full left-0 top-0 border-r-8 border-white ${percentageDone >= 99 ? 'rounded-lg' : 'rounded-tl-lg rounded-bl-lg'
								}`}
							initial={{ width: 0 }}
							// enter={{ width: 1 }}
							animate={{ width: `${percentageDone}%` }}
						/>
						<div className={`absolute font-bold w-full flex items-center h-full z-50`} transition={{ delay: 0.25 }}>
							<motion.span
								className={'absolute px-3'}
								initial={{ x: 0, opacity: 0 }}
								transition={{ delay: 0.1 }}
								// enter={{ x: 0, opacity: 1 }}
								animate={{
									opacity: 1,
									left: percentageDone < 75 ? `${percentageDone}%` : 0,
									color: percentageDone < 75 ? 'rgb(255,255,255)' : 'rgb(0,0,0)',
								}}
							>
								{moment(activeMessage.date).format('D MMM YY')}
							</motion.span>
						</div>
					</div>
				</div>
			) : (
				<div className={'absolute sm:hidden top-20 flex items-center justify-center w-full max-w-full flex-row p-3 bg-gray-900 z-40'}>
					<div className={'relative bg-gradient-to-l from-gray-700 via-gray-600 to-gray-900 inset-0 h-8 w-full rounded-lg'}>
						<motion.span
							className={`absolute bg-gray-200 z-40 h-full left-0 top-0 border-r-8 border-white ${percentageDone <= 1 ? 'rounded-lg' : 'rounded-tr-lg rounded-br-lg'
								}`}
							initial={{ width: 0 }}
							// enter={{ width: 1 }}
							animate={{ left: `${100 - percentageDone}%`, width: `${percentageDone + 0.2}%` }}
						/>
						<div className={`absolute font-bold w-full flex items-center h-full z-50`} transition={{ delay: 0.25 }}>
							<motion.span
								className={'absolute px-3'}
								initial={{ x: 0, opacity: 0 }}
								transition={{ delay: 0.1 }}
								// enter={{ x: 0, opacity: 1 }}
								animate={{
									opacity: 1,
									left: percentageDone > 25 ? `${100 - percentageDone}%` : 0,
									color: percentageDone > 25 ? 'rgb(0,0,0)' : 'rgb(255,255,255)',
								}}
							>
								{moment(activeMessage.date).format('D MMM YY')}
							</motion.span>
						</div>
					</div>
				</div>
			)}

			{/* END mobile timeline */}
		</>
	)
}

export default ChapterControl
