import React, { useContext } from 'react'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'
import EventBox from '@components/scrollytelling/whatsapp-lebanon/event-box'
import MessageBox from '@components/scrollytelling/whatsapp-lebanon/message-box'
import { motion, AnimatePresence } from 'framer-motion'
import { isMobile } from 'react-device-detect'
import moment from 'moment'

const swipeConfidenceThreshold = 10000
const swipePower = (offset, velocity) => {
	return Math.abs(offset) * velocity
}

const NotificationCentre = ({ getImage }) => {
	const {
		lang,
		currentScene,
		allMessages,
		allEvents,
		activeMessage,
		previousMessage,
		nextMessage,
		isFullScreenSlide,
		currentEvent,
		personBioIsOpen,
	} = useContext(StorylineContext)

	let previousMessages = allMessages.filter((el) => el.timestamp * 1000 < activeMessage.timestamp * 1000).sort((a, b) => a.timestamp * 1000 - b.timestamp * 1000).splice(-10)

	if (currentScene?.id !== 99) {
		const date = activeMessage?.date
		const dayBefore = moment(date).subtract(1, 'd')
		const dayAfter = moment(date).add(1, 'd')
		if (moment(currentScene?.date).isBetween(dayBefore, dayAfter)) {
			previousMessages = previousMessages.filter((el) => moment(el.date).isBetween(dayBefore, dayAfter))
		}
	}

	const currentlyVisibleEvent = allEvents.filter((el) => el.date === activeMessage.date)

	return (
		<AnimatePresence>
			{/* START displaying the events */}
			{currentEvent && currentlyVisibleEvent.length > 0 && <EventBox />}
			{/* END displaying the events */}

			<motion.div
				className={`bg-transparent absolute left-0 bottom-16 pb-4 sm:pb-0 sm:bottom-auto w-full h-full z-50 ${isMobile ? '' : 'pointer-events-none'}`}
				transition={{
					x: { type: 'spring', stiffness: 300, damping: 30 },
					opacity: { duration: 0.2 },
				}}
				key={'notification-centre'}
				drag={isMobile ? 'x' : null}
				dragConstraints={isMobile ? { left: 0, right: 0 } : { top: 0, bottom: 0 }}
				dragElastic={1}
				onDragEnd={(e, { offset, velocity }) => {
					const swipe = swipePower(offset.x, velocity.x)

					if (swipe < -swipeConfidenceThreshold) {
						if (lang === 'ar-LB') {
							previousMessage()
						} else {
							nextMessage()
						}
					} else if (swipe > swipeConfidenceThreshold) {
						if (lang === 'ar-LB') {
							nextMessage()
						} else {
							previousMessage()
						}
					}
				}}
			>
				{/* START displaying NORMAL MESSAGES */}
				{!isFullScreenSlide && !personBioIsOpen && (
					<motion.div
						className={
							'w-full sm:w-1/2 lg:w-2/5 xl:w-1/3 2xl:w-1/4 left-0 sm:left-auto right-0 sm:right-8 bottom-0 sm:bottom-0 sm:top-0 h-full absolute z-30 sm:flex sm:items-end sm:pb-4'
						}
						initial={{
							opacity: 0,
							x: '100%',
						}}
						// enter={{ opacity: 1 }}
						animate={{ opacity: 1, x: activeMessage.bigEventScene ? '100%' : 0 }}
						exit={{ opacity: 0, x: '100%' }}
						transition={{ delay: '0.1', duration: 0.5 }}
					>
						{/* <h2 className={'text-white text-center'}>Message centre</h2> */}
						<div className={'p-5 sm:p-10 sm:pb-20'} key={`message-centre-container-${activeMessage.id}`}>
							{!isMobile && (
								<>
									{previousMessages.map((el, i) => {
										// console.log(previousMessages.length)
										return (
											<MessageBox
												numberOfPrevMessages={previousMessages.length}
												getImage={getImage}
												message={el}
												i={previousMessages.length - i}
												key={`wa-message-${i}`}
												id={`wa-message-${i}`}
											/>
										)
									})}
								</>
							)}
							{activeMessage.type !== 'event' && <MessageBox getImage={getImage} message={activeMessage} isActive />}
						</div>
					</motion.div>
				)}
				{/* END displaying NORMAL MESSAGES */}
			</motion.div>
		</AnimatePresence>
	)
}

export default NotificationCentre
