import React, { useContext } from 'react'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'

const CentredTextContainer = ({ children }) => {
	const { currentDir } = useContext(StorylineContext)

	return (
		<div
			className={'mx-5 sm:mx-20 h-full bg-white p-10 overflow-y-scroll shadow-2xl max-w-8xl hand-drawn-el'}
			dir={currentDir}
			style={{ maxHeight: 'calc((70vh * 1) - 80px)' }}
		>
			{children}
		</div>
	)
}

export default CentredTextContainer
