import React, { useContext, useState, useEffect } from 'react'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'
import { motion, AnimatePresence } from 'framer-motion'
import { isMobile } from 'react-device-detect'
import CHARACTERS from '@data/whatsapp-lebanon/character-data'
import { GatsbyImage } from 'gatsby-plugin-image'

const CharacterScene = ({ getImage }) => {
	const [isMobileSSR, setIsMobileSSR] = React.useState(false)

	React.useEffect(() => {
		setIsMobileSSR(isMobile)
	}, [])

	const { activeMessage, isFullScreenSlide, setPersonBioIsOpen, lang, allEvents } = useContext(StorylineContext)
	const [currentId, setCurrentId] = useState(0)

	useEffect(() => {
		if (currentId !== activeMessage.id) {
			setPersonBioIsOpen(false)
		}
		setCurrentId(activeMessage.id)
	}, [activeMessage])

	const currentlyVisibleEvent = allEvents.filter((el) => el.date === activeMessage.date).length > 0

	return (
		<AnimatePresence>
			{!isFullScreenSlide && activeMessage.from.characterId !== 0 && (
				<motion.div
					key={`${activeMessage.from.characterId}`}
					initial={{ x: '-100%' }}
					animate={{ x: activeMessage.bigEventScene ? '-100%' : 0 }}
					// enter={{ x: activeMessage.bigEventScene ? '-100%' : 0 }}
					exit={{ x: '-100%', y: 0 }}
					transition={{
						duration: 0.4,
						delay: 0.25,
					}}
					className={`flex absolute justify-center items-end z-40 h-full top-0 bottom-0 sm:w-1/3 md:w-1/4 lg:w-1/4 xl:w-1/5 max-w-md`}
				>
					{/* Head and hands combination with animation */}
					<motion.div
						initial={{
							y: 0,
							x: 0,
						}}
						animate={{
							y: activeMessage.bigEventScene ? '100%' : 0,
							x: 0,
							y: currentlyVisibleEvent ? (isMobileSSR ? '1%' : '5%') : 0,
						}}
						// enter={{ y: activeMessage.bigEventScene ? '100%' : 0 }}
						exit={{ y: 0 }}
						transition={{
							delay: 0.5,
						}}
					>
						{/* BODY */}
						<motion.div
							className={'mb-0 bottom-10 sm:mb-28'}
							style={{ maxWidth: isMobileSSR ? '120px' : '220px' }}
							animate={{ y: [0, -10, 0] }}
							transition={{ repeat: Infinity, duration: 5 }}
						>
							<GatsbyImage alt={'Character avatar illustration (head)'} image={getImage(`character-${activeMessage.from.characterId}-body`)} />
							{/* <motion.img initial={{ y: 0 }} width={'100%'} src={require(`./imgs/character-${activeMessage.from.characterId}-body.png`).default} /> */}
						</motion.div>
						{/* HANDS */}
						<motion.div
							className={'absolute bottom-10 sm:bottom-28'}
							style={{ maxWidth: isMobileSSR ? '120px' : '220px' }}
							initial={{ y: 0 }}
							animate={{ y: isFullScreenSlide ? (isMobileSSR ? 80 : 0) : 0 }}
							// enter={{ y: isFullScreenSlide ? 80 : 0 }}
							exit={{ y: isMobileSSR ? 0 : -80 }}
							transition={{
								delay: 0,
								type: 'keyframes',
							}}
						>
							<motion.div
								id={'hands-container'}
								initial={isMobileSSR ? null : { x: 0, y: -5, opacity: 1 }}
								animate={
									isMobile
										? null
										: {
											y: -25,
											x: 10,
											skew: '5deg, 0',
										}
								}
								transition={{ repeat: Infinity, duration: 6, repeatType: 'reverse' }}
								key={`hands-animation-${activeMessage.from.characterId}`}
							>
								<GatsbyImage
									alt={'Character avatar illustration (feature)'} image={getImage(`character-${activeMessage.from.characterId}-hands`)}
								/>
								{/* <img src={require(`./imgs/character-${activeMessage.from.characterId}-hands.png`).default} /> */}
							</motion.div>
						</motion.div>
					</motion.div>
					<motion.div
						initial={{ opacity: 0, y: 50 }}
						animate={{ opacity: 0.8, y: 0, opacity: 1 }}
						// enter={{ opacity: 0.8, y: 0 }}
						transition={{
							delay: 1,
						}}
						className={'flex justify-center items-end w-full h-full absolute top-0 left-0 hand-drawn-xs'}
					>
						<p className={'px-3 sm:py-1 bg-white text-black z-50 text-xl sm:text-2xl lg:text-3xl xl:text-4xl comic-font mb-0 sm:mb-28 shadow-2xl'}>
							{CHARACTERS[activeMessage.from.characterId].name[lang]}
						</p>
					</motion.div>
				</motion.div>
			)}
		</AnimatePresence>
	)
}

export default CharacterScene
