import React, { useContext } from 'react'
import { GatsbyImage } from 'gatsby-plugin-image'
import { motion } from 'framer-motion'
import StorylineContext from '@store/whatsapp-lebanon/storyline-context'

const AnimatedBackground = ({ getImage }) => {
	const { activeMessage, currentScene } = useContext(StorylineContext)

	const Scene = ({ data, i, animate }) => {
		return (
			<motion.div
				style={{ zIndex: `${900 + parseInt(i)}` }}
				initial={animate ? data.initial || { opacity: 0, y: '100%' } : data.animate}
				animate={animate ? data.animate || { opacity: 1, y: 0 } : null}
				// enter={animate ? data.animate || { opacity: 1, y: 0 } : null}
				transition={animate ? data.transition || { delay: data.delay } : { duration: 0, delay: 0 }}
				className={'w-full h-full absolute'}
			>
				<GatsbyImage alt={`Background image`} image={getImage(data.image)} className={'absolute top-0 left-0 w-full h-full'} />
			</motion.div>
		)
	}

	return (
		<motion.div className={'absolute top-0 left-0 w-full h-full z-1'} key={`animated-bg-${activeMessage.backgroundScene}`}>
			<div className={'relative w-full h-full'}>
				{currentScene?.scenes?.map((scene, i) => {
					return <Scene data={scene} i={i} animate={activeMessage.fullScreenEvent} key={`animated-background-${i}`} />
				})}
			</div>
		</motion.div>
	)
}

export default AnimatedBackground
