import React, { useContext, useState, useEffect } from 'react'

import { motion } from 'framer-motion'
import { isMobile } from 'react-device-detect'
import RichText from '@components/scrollytelling/whatsapp-lebanon/rich-text'
import moment from 'moment'
import '@components/scrollytelling/whatsapp-lebanon/ar-locale'

import StorylineContext from '@store/whatsapp-lebanon/storyline-context'
import ImageViewer from '@components/scrollytelling/whatsapp-lebanon/image-viewer'
import VideoViewer from '@components/scrollytelling/whatsapp-lebanon/video-viewer'

function swapBracket(br) {
	switch (br) {
		case '(':
			return ')'
		case ')':
			return '('
		case '{':
			return '}'
		case '}':
			return '{'
		case '[':
			return ']'
		case ']':
			return '['
	}
}

function flipBracketsDirection(str) {
	return str.replace(/[\(\)\[\]\{\}]/g, swapBracket)
}

const MessageBox = ({ isActive, message, i, getImage }) => {
	const { currentLangData, currentDir, lang, nextMessage, jumpToMessage, isIOS } = useContext(StorylineContext)
	const messageDisplayDate = moment(message.timestamp * 1000)
		.locale(lang === 'ar-LB' ? 'ar' : 'en-GB')
		.format('D MMM YY')

	const [isMobileSSR, setIsMobileSSR] = useState(false)

	useEffect(() => {
		setIsMobileSSR(isMobile)
	}, [])

	const isIncomingOffsetValueX = message.isIncoming ? -150 : 0
	const incoming = message.isIncoming

	let messageText = message.text[lang]

	if (lang === 'ar-LB') {
		messageText = flipBracketsDirection(messageText)
	}

	return (
		<>
			{message.type === 'event' ? (
				<div className={'hand-drawn-sm'}>
					<motion.div
						initial={{
							y: 0,
							x: 0,
							opacity: 1,
							background: '#e3be7e',
						}}
						animate={{
							y: 0,
							opacity: 1,
							// rotate: currentRotation,
							background: '#e3be7e',
						}}
						className={`relative flex items-center justify-center w-full h-10 my-10`}
					>
						<h4>
							<strong>
								{message.eventTitle[lang]} –{' '}
								<span className={`bg-gray-900 text-white px-2 py-1 ${lang === 'ar-LB' ? '' : 'comic-font'}`}>
									{moment(message.date)
										.locale(lang === 'ar-LB' ? 'ar' : 'en-GB')
										.format('D MMM YYYY')}
								</span>
							</strong>
						</h4>
					</motion.div>
				</div>
			) : (
				<motion.div
					key={`prev-message-box-${isActive ? 'active' : i}`}
					initial={{
						opacity: isActive ? 0 : 0.8,
						x: isActive ? 50 : incoming ? isIncomingOffsetValueX : 0,
						y: isActive || isMobile ? 0 : 50,
						background: incoming ? '#f5f5f5' : '#E0FCD6',
					}}
					animate={{
						opacity: isActive ? 1 : 0.8,
						scale: isMobile ? 1 : isActive ? 1.05 : 1,
						x: !isMobile ? isIncomingOffsetValueX : 0,
						y: 0,
						background: incoming ? '#f5f5f5' : '#E0FCD6',
					}}
					exit={{ opacity: 0, x: 50, y: 0 }}
					className={`absolute left-2 m-4 sm:left-auto right-2 sm:right-auto sm:m-auto bottom-40 sm:bottom-0 sm:relative p-5 sm:mb-8 cursor-pointer pointer-events-auto rounded-2xl overflow-hidden shadow-2xl bubble-message ${isActive ? 'active sm:mt-10 sm:mb-11' : ''
						}`}
					onClick={() => {
						isMobile ? nextMessage() : isActive ? nextMessage() : jumpToMessage(message.timestamp)
					}}
					transition={{
						delay: isActive ? 0.075 : 0.05 + i * 0.05,
					}}
				>
					<div className={`flex justify-between items-center ${incoming ? 'bg-gray-300' : 'bg-gray-800'} py-2 px-3 rounded-t-xl absolute top-0 left-0 w-full`}>
						<div className={`flex items-center gap-3 ${lang === 'en-GB' ? 'flex-row' : 'flex-row-reverse'}`}>
							<span className={`bg-burgundy px-3 py-1 text-base uppercase font-bold rounded-2xl text-white mr-2 ${lang === 'en-GB' ? 'comic-font' : ''}`}>
								{currentLangData?.app.from}
							</span>
							<span className={`${lang === 'en-GB' ? 'comic-font' : ''} ${incoming ? 'text-black' : 'text-white'}`}>{message.from.name[lang]}</span>
						</div>
						{message.id !== 0 && (
							<span className={`${incoming ? 'text-gray-500' : 'text-gray-400'}`}>
								{messageDisplayDate}
							</span>
						)}
						<div className={`flex items-center gap-3 ${lang === 'en-GB' ? 'flex-row' : 'flex-row-reverse'}`}>
							<span className={`bg-black px-3 py-1 text-base uppercase font-bold rounded-2xl text-white mr-2 ${lang === 'en-GB' ? 'comic-font' : ''}`}>
								{currentLangData?.app.to}
							</span>
							<span className={`${lang === 'en-GB' ? 'comic-font' : ''} ${incoming ? 'text-black' : 'text-white'}`}>{message.to.name[lang]}</span>
						</div>
					</div>
					<div className={'px-2 pb-2 pt-12'} dir={currentDir}>
						{message.type === 'text' && (
							<div className={`lg:text-3xl xl:text-3xl 2xl:text-4xl inherit-font w-full message-box--text`}>
								<img src={require('./imgs/transparent.png').default} className={'w-screen max-w-full h-0 block'} width='100%' height='1px' />
								<RichText className={'text-2xl'} text={messageText} />
							</div>
						)}
						{message.type === 'photo' && <ImageViewer caption={messageText} image={getImage(message.image[lang] || 'output_1')} />}
						{message.type === 'video' && <VideoViewer caption={messageText} video={message.image[lang]} />}
					</div>
				</motion.div>
			)}
		</>
	)
}

export default MessageBox
